import btcIcon from '../../../../assets/img/btc-icon.svg'
import selectIcon from '../../../../assets/img/arrow-down.svg'
import './symbolInfo.less'
import DeriNumberFormat from '../../../../utils/DeriNumberFormat'
import { firstLetterUppercase } from '../../../../utils/utils'
import Icon from '../../../../components/Icon/Icon'

export default function SymbolInfo({trading,lang}){
  return (
    <div className='symbol-info'>
      <div className='main'>
        <div className='s-left'>
          <div className='zone'>{firstLetterUppercase(trading.symbolInfo.zone)}</div>
          <div className='symbol-name'><img src={btcIcon} alt='btc'/>{trading.symbolInfo && trading.symbolInfo.symbol}</div>
        </div>
        <div className='index-area'>
          <div className='index-area-wraper'>
            <div className='rate'>
              {trading.symbolInfo && <Icon token={trading.symbolInfo.volumn24h > 0 ? 'arrow-up' : 'arrow-down'} alt='' />}
              <DeriNumberFormat value={trading.symbolInfo.volumn24h}/></div>
            <div className='index-value'><DeriNumberFormat value={trading.symbolInfo.markPrice || trading.markPrice} decimalScale={2}/></div>
          </div>
          <div className='select-icon'><img data-tip data-for='symbol-panel' data-event='click' src={selectIcon} alt='select'/></div>
        </div>
      </div>
    </div>
  )
}